<template>
  <div class="tiny-calendar">
    <div class="left">
      <div class="day-box">
        <span class="day" v-text="day"></span>
      </div>
      <div class="yr-n-mo-box">
        <span class="yr" v-text="year"></span>
        <span>&nbsp;/&nbsp;</span>
        <span class="mo" v-text="month"></span>
      </div>
    </div>
    <div class="right">
      <div class="week">
        <span></span>
        <span class="mo" v-text="week"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TinyCalendar",
  components: {},
  data() {
    return {
      year: "",
      month: "",
      day: "",
      week: "",
      timer: "",
    };
  },
  computed: {},
  watch: {},
  methods: {
    showDate(objD, option) {
      var str;
      var yy = objD.getYear();
      if (yy < 1900) yy = yy + 1900;
      var MM = objD.getMonth() + 1;
      // if (MM == 0) MM = "";
      // if (MM == 1) MM = "";
      // if (MM == 2) MM = "";
      // if (MM == 3) MM = "";
      // if (MM == 4) MM = "";
      // if (MM == 5) MM = "";
      // if (MM == 6) MM = "";
      // if (MM == 7) MM = "";
      // if (MM == 8) MM = "";
      // if (MM == 9) MM = "";
      // if (MM == 10) MM = "";
      // if (MM == 11) MM = "";
      // if (MM < 10) MM = '0' + MM;
      var dd = objD.getDate();
      if (dd < 10) dd = "0" + dd;
      var ww = objD.getDay();
      if (ww == 0) ww = "";
      if (ww == 1) ww = "";
      if (ww == 2) ww = "";
      if (ww == 3) ww = "";
      if (ww == 4) ww = "";
      if (ww == 5) ww = "";
      if (ww == 6) ww = "";
      if (option == "y") {
        str = yy;
      } else if (option == "m") {
        str = MM;
      } else if (option == "d") {
        str = dd;
      } else if (option == "w") {
        str = ww;
      } else {
        str = yy + "-" + MM + "-" + dd + " " + ww;
      }
      return str;
    },
    tick() {
      let today = new Date();
      //   this.localtime = this.showTime(today);
      this.year = this.showDate(today, "y");
      this.month = this.showDate(today, "m");
      this.day = this.showDate(today, "d");
      this.week = this.showDate(today, "w");
    },
  },
  created() {},
  mounted() {
    this.tick();
    this.timer = setInterval(() => {
      this.tick();
    }, 1000);
  },
  beforeDestroy() {},
};
</script>

<style scoped>
.tiny-calendar {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.day {
  font-size: 3em;
}
.week {
  font-size: 2em;
}
</style>